<template>
  <div id="aplayer-container">
    <div id="aplayer"></div>
  </div>
</template>

<script>
import APlayer from "aplayer";
import "aplayer/dist/APlayer.min.css";
export default {
  mounted() {
    this.initAudio();
  },
  methods: {
    initAudio() {
      this.ap = new APlayer({
        container: document.getElementById("aplayer"),
        audio: this.audio,
        ...this.info,
      });
    },
  },
  data() {
    return {
      ap: null,
      info: {
        fixed: true,
        listFolded: true,
        autoplay: false,
        preload: "auto",
        loop: "one",
        order: "list",
        lrcType: 3,
        volume: 0.728,
      },
      audio: [
        {
          name: "Everything Goes On (Áæ§Êòü‰æùÊóß)",
          artist: "LOL",
          url:
            window.location.origin +
            "/bilibili-frontend/birthday21/dist/music.mp3",
          cover:
            window.location.origin +
            "/bilibili-frontend/birthday21/dist/music.jpg",
          lrc:
            window.location.origin +
            "/bilibili-frontend/birthday21/dist/music.lrc",
        },
      ],
    };
  },
};
</script>

<style scoped>
#aplayer-container {
  position: absolute;
  bottom: 0;
  z-index: 7;
  left: 0;
  right: 0;
  margin: auto;
  width: 700px;
}
</style>